// Styling for the returned books images & descriptions

.bookGallery {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(5, auto);
    grid-gap: 50px;
    grid-auto-rows: 1fr;
    grid-auto-columns: 1fr;
    grid-auto-flow: dense;
    justify-content: space-between;
    overflow: hidden;
    padding-bottom: 50px;
}

.bookGallery {
        li {
            max-width: 250px;
            max-height: 375px; 
        }
        
        button {
            width: 100%;
            height: 100%;
            background-color: transparent;
            border: none;
        }
        img {
            width: 100%;
            height: 100%;
        }
}

.bookGallery img:hover,
.bookGallery img:focus {
    border: 1px solid $mainColor;
}


